* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  max-width: 1400px;
  display: block;
  margin: auto;
  min-height: 100vh;
  background: #191919;
  font-family: sans-serif;
}

body::-webkit-scrollbar {
  width: 12px;
}

body::-webkit-scrollbar-track {
  background: #191d2b;
}

body::-webkit-scrollbar-thumb {
  background-color: #27ae60;
  border-radius: 20px;
  border: 3px solid #191d2b;
}


